import { createRouter, createWebHistory } from 'vue-router'
import HomeScreen from '@/views/home-screen/HomeScreen.vue'
import Lc from '@/views/lc/Lc.vue'
import SignIn from '@/views/sign-in/SignIn.vue'
import Registration from '@/views/registration/Registration.vue'
import RegistrationSuccess from '@/views/registration/registration-success/RegistrationSuccess.vue'
import PassRecovery from '@/views/pass-recovery/PassRecovery.vue'
import PassRecoverySuccess from '@/views/pass-recovery/pass-recovery-success/PassRecoverySuccess.vue'
import TrackPage from '@/views/track-page/TrackPage.vue'
import HistoryLetters from '@/views/history-letters/HistoryLetters.vue'
import PersonalLetters from '@/views/history-letters/personal-letters/PersonalLetters.vue'
import FinesLetters from '@/views/history-letters/fines-letters/FinesLetters.vue'
import AttorneyLetters from '@/views/history-letters/attorney-letters/AttorneyLetters.vue'
import Contacts from '@/views/contacts/Contacts.vue'
import ContactsUln from '@/views/contacts/contacts-uln/ContactsUln.vue'
import ContactsDmt from '@/views/contacts/contacts-dmt/ContactsDmt.vue'
import Faq from '@/views/faq/Faq.vue'
import Settings from '@/views/settings/Settings.vue'
import SettingsContact from '@/views/settings/settings-contact/SettingsContact.vue'
import SettingsPersonal from '@/views/settings/settings-personal/SettingsPersonal.vue'
import SettingsPayment from '@/views/settings/settings-payment/SettingsPayment.vue'
import SettingsSecurity from '@/views/settings/settings-security/SettingsSecurity.vue'

const routes = [{
    path: '/',
    name: 'HomeScreen',
    component: HomeScreen
  },
  {
    path: '/contacts',
    name: 'Contacts',
    component: Contacts,
    children: [{
        path: '',
        redirect: '/contacts/uln',
      },
      {
        path: 'uln',
        component: ContactsUln
      },
      {
        path: 'dmt',
        component: ContactsDmt,
      }
    ],
  },
  {
    path: '/lc',
    name: 'Lc',
    component: Lc
  },
  {
    path: '/settings',
    name: 'Settings',
    component: Settings,
    children: [{
        path: '',
        redirect: '/settings/contact',
      },
      {
        path: 'contact',
        component: SettingsContact
      },
      {
        path: 'personal',
        component: SettingsPersonal
      },
      {
        path: 'payment',
        component: SettingsPayment
      },
      {
        path: 'security',
        component: SettingsSecurity,
      }
    ],
  },
  {
    path: '/sign-in',
    name: 'SignIn',
    component: SignIn
  },
  {
    path: '/registration',
    name: 'Registration',
    component: Registration,
  },
  {
    path: '/registration-success',
    name: 'RegistrationSuccess',
    component: RegistrationSuccess
  },
  {
    path: '/pass-recovery',
    name: 'PassRecovery',
    component: PassRecovery,
  },
  {
    path: '/pass-recovery-success',
    name: 'PassRecoverySuccess',
    component: PassRecoverySuccess,
    props: true,
  },
  {
    path: '/track-page',
    name: 'TrackPage',
    component: TrackPage,
  },
  {
    path: '/faq',
    name: 'Faq',
    component: Faq,
  },
  {
    path: '/history-letters',
    name: 'HistoryLetters',
    component: HistoryLetters,
    children: [{
        path: '',
        redirect: '/history-letters/personal',
      },
      {
        path: 'personal',
        component: PersonalLetters
      },
      {
        path: 'attorney',
        component: AttorneyLetters,
      },
      {
        path: 'fines',
        component: FinesLetters,
      }
    ],
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  // redirect to login page if not logged in and trying to access a restricted page
  const publicPages = ['/', '/sign-in', '/registration', '/pass-recovery', '/contacts', '/contacts/uln', '/contacts/dmt', '/registration-success', '/pass-recovery-success'];
  const authRequired = !publicPages.includes(to.path);
  const loggedIn = localStorage.getItem('user');

  if (authRequired && !loggedIn) {
    return next('/sign-in');
  }

  next();
})

export default router