<script>
import { ref } from "vue";
import { mapState, mapActions } from "vuex";
import Bg from "@/images/lc/lc_bg.jpg";
import BgMedium from "@/images/lc/lc_bg1366.jpg";
import BgSmall from "@/images/lc/lc_bg1024.jpg";
import HeaderLc from "@/components/header/header-lc/HeaderLc.vue";

import "./Lc.css";

const modalVisible = ref(false);
const modalSelect = ref(true);
const modalSubdivision = ref(false);
const tabsVisible = ref(true);
const emailVisible = ref(false);

const openModal = (select) => {
  modalVisible.value = true;
  if (select) {
    modalSelect.value = true;
  } else {
    modalSelect.value = false;
    getInSubdivision();
  }
};

const exitModal = () => {
  modalVisible.value = false;
  modalSelect.value = true;
  modalSubdivision.value = false;
  emailVisible.value = false;
  zeroingCheckBox();
};

const getInSubdivision = () => {
  modalSubdivision.value = true;
};

const zeroingCheckBox = () => {
  document.querySelectorAll("input[type=checkbox]").forEach((item) => {
    item.checked = false;
  });
};

const getClassForModal = () => {
  let className = "";

  if (!modalSelect.value) {
    className += "lc__modal-wrapper_date";
  }

  if (modalSubdivision.value) {
    className += " lc__modal-wrapper_subdivision";
  }

  if (emailVisible.value) {
    className += " lc__modal-wrapper_email-success";
  }
  return className;
};

const selectTab = (event) => {
  if (event.target.className.indexOf("lc__tab-item") !== -1) {
    document.querySelectorAll(".lc__tab-item").forEach((item) => {
      item.classList.remove("lc__tab-item_active");
    });
    event.target.classList.add("lc__tab-item_active");
    tabsVisible.value = !tabsVisible.value;
  }
};

const requestEmail = () => {
  emailVisible.value = true;
};

export default {
  name: "Lc",
  components: {
    HeaderLc,
  },
  data: () => {
    return {
      Bg,
      BgSmall,
      BgMedium,
      modalVisible,
      modalSelect,
      modalSubdivision,
      tabsVisible,
    };
  },
  computed: {
    ...mapState({
      account: (state) => state.account,
    }),
    getClassForModal,
  },
  methods: {
    ...mapActions("account", ["logout"]),
    openModal,
    exitModal,
    getInSubdivision,
    selectTab,
    requestEmail,
  },
};
</script>