<template>
  <div class="tabs__container" ref="tabsContainer">
    <div ref="tabs" class="tabs">
      <router-link
        v-for="(item, index) in this.items"
        :key="item"
        :to="item.path"
        class="tabs__item"
        :class="(index===activeIndex-1 ? ' tabs__item_active' : '')"
      >{{item.name}}</router-link>
    </div>
  </div>
</template>

<script>
import "./Tabs.css";

export default {
  name: "Tabs",
  props: {
    items: Array,
    activeIndex: Number,
  }
};
</script>