<template>
  <div class="fine-item" :class="getItemClasses">
    <div class="fine-item__checkbox">
      <input type="checkbox" />
      <svg width="12" height="9" viewBox="0 0 12 9" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path
          d="M1 4.06114L4.39416 7.32485L10.973 1"
          stroke="white"
          stroke-width="1.5"
          stroke-linecap="round"
        />
      </svg>
    </div>
    <div class="fine-item__favorite">
      <input type="checkbox" :id="(index)" />
      <label :for="index" v-on:click.stop>
        <svg
          width="23"
          height="23"
          viewBox="0 0 23 23"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M11.2747 2.20104C11.3654 2.01205 11.6346 2.01205 11.7253 2.20104L13.9161 6.76121L14.5921 6.43643L13.9161 6.76121C14.1712 7.29223 14.6764 7.65925 15.2602 7.73778L20.2742 8.41215C20.482 8.44009 20.5652 8.69603 20.4135 8.84078L16.7535 12.3335C16.3273 12.7402 16.1343 13.3341 16.2401 13.9136L16.9779 13.779L16.2401 13.9136L17.1481 18.8906C17.1857 19.0968 16.968 19.255 16.7835 19.1555L12.3307 16.7539C11.8122 16.4743 11.1878 16.4743 10.6693 16.7539L6.21651 19.1555C6.03197 19.255 5.81427 19.0968 5.8519 18.8906L6.75994 13.9136C6.86568 13.3341 6.67272 12.7402 6.24652 12.3335L2.58653 8.84078C2.43484 8.69603 2.518 8.44009 2.7258 8.41215L7.73976 7.73778C8.32362 7.65925 8.82879 7.29223 9.0839 6.76121L8.40786 6.43643L9.0839 6.76121L11.2747 2.20104Z"
            stroke="#B7B7B7"
            stroke-width="1.5"
          />
        </svg>
      </label>
    </div>
    <div class="fine-item__information-container">
      <div class="fine-item__information-item">
        <span>{{item.to}}</span>
      </div>
      <div class="fine-item__information-item">{{item.from}}</div>
      <div class="fine-item__information-item">{{item.date}}</div>
      <div class="fine-item__information-item">{{item.attachment}}</div>
    </div>
    <button class="fine-item__btn">Оплатить</button>
  </div>
  <div class="fine-item fine-item_mobile" :class="getItemClasses">
    <div class="fine-item__field-container">
      <div class="fine-item__field-item">
        <div class="fine-item__item-description">Кому</div>
        <div class="fine-item__information-item">{{item.to}}</div>
      </div>
      <div class="fine-item__field-item">
        <div class="fine-item__item-description">От кого</div>
        <div class="fine-item__information-item">{{item.from}}</div>
      </div>
      <div class="fine-item__field-item">
        <div class="fine-item__item-description">Дата получения письма</div>
        <div class="fine-item__information-item">{{item.date}}</div>
      </div>
      <div class="fine-item__field-item">
        <div class="fine-item__item-description">Наличие вложения</div>
        <div class="fine-item__information-item">{{item.attachment}}</div>
      </div>
    </div>
    <button class="fine-item__btn">Оплатить</button>
    <div class="fine-item__filter-container">
      <div class="fine-item__favorite" v-on:click.stop>
        <input type="checkbox" :id="(index+1)" />
        <label :for="index+1" v-on:click.stop>
          <svg
            width="23"
            height="23"
            viewBox="0 0 23 23"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M11.2747 2.20104C11.3654 2.01205 11.6346 2.01205 11.7253 2.20104L13.9161 6.76121L14.5921 6.43643L13.9161 6.76121C14.1712 7.29223 14.6764 7.65925 15.2602 7.73778L20.2742 8.41215C20.482 8.44009 20.5652 8.69603 20.4135 8.84078L16.7535 12.3335C16.3273 12.7402 16.1343 13.3341 16.2401 13.9136L16.9779 13.779L16.2401 13.9136L17.1481 18.8906C17.1857 19.0968 16.968 19.255 16.7835 19.1555L12.3307 16.7539C11.8122 16.4743 11.1878 16.4743 10.6693 16.7539L6.21651 19.1555C6.03197 19.255 5.81427 19.0968 5.8519 18.8906L6.75994 13.9136C6.86568 13.3341 6.67272 12.7402 6.24652 12.3335L2.58653 8.84078C2.43484 8.69603 2.518 8.44009 2.7258 8.41215L7.73976 7.73778C8.32362 7.65925 8.82879 7.29223 9.0839 6.76121L8.40786 6.43643L9.0839 6.76121L11.2747 2.20104Z"
              stroke="#B7B7B7"
              stroke-width="1.5"
            />
          </svg>
        </label>
      </div>
      <div class="fine-item__checkbox" v-on:click.stop>
        <input type="checkbox" v-model="checked"/>
        <svg
          width="12"
          height="9"
          viewBox="0 0 12 9"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M1 4.06114L4.39416 7.32485L10.973 1"
            stroke="white"
            stroke-width="1.5"
            stroke-linecap="round"
          />
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
import "./FineItem.css";

function getItemClasses() {
  let className = "";

  if (this.class) {
    className += this.class;
  }

  if(this.checked) {
    className += " fine-item_checked"
  }

  return className;
}

export default {
  name: "FineItem",
  props: {
    item: Object,
    index: Number,
    class: String,
  },
  computed: {
    getItemClasses,
  },
  data: () => {
    return {
      checked: false,
    }
  }
};
</script>