<template>
  <div class="pass-recovery">
    <div class="pass-recovery__logo">
      <router-link to="/"><img :src="Logo" alt="pass-recovery__logo" /></router-link>
    </div>
    <div class="pass-recovery__wrapper pass-recovery__wrapper_active">
      <div class="pass-recovery__title">Восстановление пароля</div>
      <form v-on:submit.prevent="handleSubmit">
        <div class="pass-recovery__field-name">E-mail</div>
        <div class="pass-recovery__error-container">
          <input
            class="pass-recovery__input"
            type="email"
            placeholder="Введите e-mail"
            v-model="email"
            @keydown.space.prevent
            required
          />
          <div class="pass-recovery__error">{{account.status.error}}</div>
        </div>
        <button :disabled="isDisabled" type="submit" class="pass-recovery__btn">Продолжить</button>
        <router-link to="/sign-in" class="pass-recovery__sign-in">Вернуться к авторизации</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Logo from "@/images/signIn/logo.png";

import "./PassRecovery.css";

function handleSubmit(e) {
  e.preventDefault();
  const { email } = this;

  if (email) {
    this.restorePass(this.email);
  }
}

export default {
  name: "PassRecovery",
  data: () => {
    return {
      Logo,
      email: "",
    };
  },
  methods: {
    handleSubmit,
    ...mapActions("account", ["restorePass"]),
  },
  computed: {
    isDisabled: function () {
      if (this.email) {
        return false;
      }
      return true;
    },
    ...mapState({
      account: (state) => state.account,
    }),
  },
};
</script>