<template>
  <div class="settings">
    <HeaderLc v-bind:account="account" v-bind:logout="logout" v-bind:backLc="true"/>
    <div class="settings__wrapper">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import HeaderLc from "@/components/header/header-lc/HeaderLc.vue";

import "./Settings.css";

export default {
  name: "Settings",
  components: {
    HeaderLc,
  },
  computed: {
    ...mapState({
      account: (state) => state.account,
    }),
  },
  methods: {
    ...mapActions("account", ["logout"]),
  },
};
</script>