import { userService } from '../services';
import router from '../router';

const user = JSON.parse(localStorage.getItem('user'));
const state = user ? { status: { loggedIn: true }, user } : { status: {}, user: null };

const actions = {
  login({/* dispatch,*/ commit }, { email, password }) {
    commit('loginRequest', { email });
    if(userService.login(email, password)) {
      commit('loginSuccess', user);
      router.push('/lc');
    }
    /*
    userService.login(email, password)
      .then(
        user => {
          commit('loginSuccess', user);
          router.push('/lc');
        },
        error => {
          commit('loginFailure', error);
          dispatch('alert/error', error, { root: true });
        }
      );
      */
  },
  logout({ commit }) {
    router.push('/sign-in');
    userService.logout();
    commit('logout');
  },
  register({ dispatch, commit }, user) {
    commit('registerRequest', user);

    userService.register(user)
      .then(
        user => {
          commit('registerSuccess', user);
          setTimeout(() => {
            // display success message after route change completes
            dispatch('alert/success', 'Registration successful', { root: true });
          })
          router.push('/registration-success')
        },
        error => {
          commit('registerFailure', error);
          dispatch('alert/error', error, { root: true });
        }
      );
  },
  restorePass({ commit }, email) {
    commit('restorePass', email);
    userService.restorePass(email)
      .then(
        message => {
          commit('restorePassSuccess', message);
          router.push({
            name: 'PassRecoverySuccess',
            params: {
              email,
            }
          })
        },
        error => {
          commit('restorePassFailure', error);
        }
      )
  },
  resetPassword({ commit }, { token, pass, newPass }) {
    commit('resetPassword', { token, pass, newPass });
    userService.resetPassword(token, pass, newPass)
      .then(
        message => {
          commit('resetPasswordSuccess', message)
        },
        error => {
          commit('resetPasswordFailure', error)
        }
      )
  }
};

const mutations = {
  loginRequest(state, user) {
    state.status = { loggingIn: true };
    state.user = user;
  },
  loginSuccess(state, user) {
    state.status = { loggedIn: true };
    state.user = user;
  },
  loginFailure(state) {
    state.status = {};
    state.user = null;
    state.status = { error: true }
  },
  logout(state) {
    state.status = {};
    state.user = null;
  },
  restorePass() {

  },
  restorePassFailure(state) {
    state.status = { error: 'Не найдено такой почты' };
  },
  resetPassword() {},
  resetPasswordSuccess() {},
  resetPasswordFailure(state) {
    state.status = { error: 'Неверный пароль' };
  },
  registerRequest(state) {
    state.status = { registering: true };
  },
  registerSuccess(state) {
    state.status = {};
  },
  registerFailure(state) {
    state.status = { error: 'Эта почта уже используется' };
  }
};

export const account = {
  namespaced: true,
  state,
  actions,
  mutations
};