<template>
  <div class="pass-recovery">
    <div class="pass-recovery__logo">
      <router-link to="/">
        <img :src="Logo" alt="pass-recovery__logo" />
      </router-link>
    </div>
    <div class="pass-recovery__wrapper pass-recovery__wrapper_active">
      <div class="pass-recovery__title pass-recovery__title_margin">Подтверждение смены пароля</div>
      <div class="pass-recovery__sub-title">
        Ссылка для подтверждения отправлена на
        <div>{{isShowPassword}}</div>Перейдите по ссылке из письма для восстановления пароля.
      </div>
      <router-link to="/sign-in" class="pass-recovery__btn-sign">Вернуться к авторизации</router-link>
    </div>
  </div>
</template>

<script>
import Logo from "@/images/signIn/logo.png";

export default {
  name: "PassRecoverySuccess",
  props: {
    email: String,
  },
  data: () => {
    return {
      Logo,
    };
  },
  computed: {
    isShowPassword: function () {
      if (this.email) {
        const indexAt = this.email.indexOf("@");
        const hiddenEmail = this.email.split("");
        hiddenEmail.forEach((char, index) => {
          if (index > 0 && index < indexAt) {
            hiddenEmail[index] = "*";
          }
        });
        return hiddenEmail.join("");
      }
      return "";
    },
  },
};
</script>