<template>
  <div>
    <Tabs v-bind:activeIndex="4" :items="tabsItems" class="settings__tabs" />
    <div class="settings__title">Изменение пароля</div>
    <form v-on:submit.prevent="handleSubmit">
      <div class="settings__form-container">
        <div>
          <div class="settings__input-container">
            <div class="settings__input-label">Ваш текущий пароль</div>
            <input
              type="text"
              v-model="pass"
              class="settings__input"
              placeholder="Введите ваш текущий пароль"
            />
          </div>
          <div class="settings__input-container">
            <div class="settings__input-label">Новый пароль</div>
            <input
              type="text"
              v-model="newPass"
              class="settings__input"
              placeholder="Введите новый пароль"
            />
          </div>
          <div class="settings__input-container">
            <div class="settings__input-label">Повторите пароль</div>
            <input
              type="text"
              v-model="copyNewPassword"
              class="settings__input"
              placeholder="Введите новый пароль"
            />
          </div>
        </div>
      </div>
      <button type="submit" class="settings__btn">Сохранить</button>
    </form>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";

import Tabs from "@/components/subcomponents/tabs/Tabs.vue";

const tabsItems = [
  {
    name: "Контактная информация",
    path: "/settings/contact",
  },
  {
    name: "Персональные данные",
    path: "/settings/personal",
  },
  {
    name: "Платежные данные",
    path: "/settings/payment",
  },
  {
    name: "Безопасность аккаунта",
    path: "/settings/security",
  },
];

function handleSubmit() {
  const { pass, newPass, copyNewPassword } = this;
  if (pass, newPass, copyNewPassword) {
    const token = this.account.user.token;
    this.resetPassword({token, pass, newPass});
  }
}

export default {
  name: "SettingsSecurity",
  components: {
    Tabs,
  },
  data: () => {
    return {
      tabsItems,
      pass: "",
      newPass: "",
      copyNewPassword: "",
    };
  },
  methods: {
    handleSubmit,
    ...mapActions("account", ["resetPassword"]),
  },
  computed: {
    ...mapState({
      account: (state) => state.account,
    }),
  },
};
</script>