<script>
import { ref } from "vue";

import Wrapper from "@/components/subcomponents/wrapper/Wrapper.vue";
import TraceForm from "@/components/subcomponents/trace-form/TraceForm.vue";
import BurgerMenu from "@/components/subcomponents/burger-menu/BurgerMenu";
import Logo from "@/images/logo.png";
import LogoXl from "@/images/logo1024.png";
import LogoMobile from "@/images/logo320.png";

import "./NavigationBar.css";

const burgerVisible = ref(false);

const handleActiveMenu = () => {
  burgerVisible.value = !burgerVisible.value;
  const body = document.getElementsByTagName("BODY")[0];
  body.classList.toggle("body_overflow");
  const burgerBtn = document.querySelector(
    ".navigation__burger-btn_no-animation"
  );
  if (burgerBtn) {
    burgerBtn.classList.remove("navigation__burger-btn_no-animation");
  }
};

function destroyedComponent() {
  const body = document.getElementsByTagName("BODY")[0];
  body.classList.remove("body_overflow");
  this.burgerVisible = false;
}
export default {
  name: "NavigationBar",
  components: {
    Wrapper,
    TraceForm,
    BurgerMenu,
  },
  data: () => {
    return {
      burgerVisible,
      Logo,
      LogoXl,
      LogoMobile,
    };
  },
  methods: {
    handleActiveMenu,
  },
  unmounted: destroyedComponent,
};
</script>