<template>
  <div>
    <Tabs
      v-bind:activeIndex="1"
      :items="[
      {
        name: 'Ульяновск', 
        path: '/contacts/uln'
      },
      {
        name: 'Димитровград', 
        path: '/contacts/dmt'
      },
      ]"
    />
    <div class="contacts__place-container">
      <div class="contacts__place-item">
        <div class="contacts__place-description">Центр</div>
        <div class="contacts__place-address">ул. Федерации, д. 89а</div>
      </div>
      <div class="contacts__place-item">
        <div class="contacts__place-description">Заволжье</div>
        <div class="contacts__place-address">ул. Садовая, д. 2</div>
      </div>
      <div class="contacts__place-item">
        <div class="contacts__place-description">Север</div>
        <div class="contacts__place-address">ул. Юности 5а</div>
      </div>
      <div class="contacts__place-item">
        <div class="contacts__place-description">Засвияжье</div>
        <div class="contacts__place-address">ул. Рябикова, 22д</div>
      </div>
    </div>
    <div ref="map" class="contacts__map">
      <Preloader :visible="preloaderVisible"/>
    </div>
  </div>
</template>

<script>
import ymaps from "ymaps";
import { ref } from "vue";

import Tabs from "@/components/subcomponents/tabs/Tabs.vue";
import Preloader from "@/components/subcomponents/preloader/Preloader.vue";
import Pin from "@/images/pin.png";


const preloaderVisible = ref(true);



const geoObjects = [
  {
    coord: [54.328806, 48.395865],
    address: "ул. Федерации, д. 89а ",
  },
  {
    coord: [54.337159, 48.535202],
    address: "ул. Садовая, д. 2 ",
  },
  {
    coord: [54.353701, 48.365519],
    address: "ул. Юности 5а",
  },
  {
    coord: [54.284069, 48.299682],
    address: "ул. Рябикова, 22д",
  },
];
function initMap() {
  ymaps
    .load(
      "https://api-maps.yandex.ru/2.1/?apikey=b7933168-5a22-40f2-a88c-d20f09c1eaf3&lang=ru_RU"
    )
    .then((maps) => {
      const map = new maps.Map(this.$refs.map, {
        center: [54.328806, 48.395865],
        zoom: 7,
      });
      geoObjects.forEach((item) => {
        map.geoObjects.add(
          new maps.Placemark(
            item.coord,
            {
              balloonContent: `${item.address}`,
            },
            {
              iconImageHref: Pin,
              iconLayout: "default#image",
              iconImageSize: [49, 60],
              iconImageOffset: [-7, -58],
            }
          )
        );
      });
      map.controls.remove("geolocationControl");
      map.controls.remove("searchControl");
      map.controls.remove("trafficControl");
      map.controls.remove("typeSelector");
      map.controls.remove("fullscreenControl");
      map.controls.remove("rulerControl");
      map.setBounds(map.geoObjects.getBounds(), {
        checkZoomRange: true,
        zoomMargin: 9,
      });

      preloaderVisible.value = false;
    })
    .catch((error) => console.log("Failed to load Yandex Maps", error));
}

export default {
  name: "ContactsUln",
  components: {
    Tabs,
    Preloader
  },
  methods: {
    initMap,
  },
  data: () => {
    return {
      preloaderVisible
    }
  },
  mounted: initMap,
};
</script>