<template>
  <div class="history-letters">
    <HeaderLc v-bind:account="account" v-bind:logout="logout"/>
    <div class="history-letters__main">
      <div class="history-letters__wrapper history-letters__wrapper_display">
        <div class="history-letters__title-container">
          <div class="history-letters__title">История писем</div>
          <div class="history-letters__count">67</div>
        </div>
        <div class="history-letters__container">
          <router-view></router-view>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderLc from "@/components/header/header-lc/HeaderLc.vue";
import { mapState, mapActions } from "vuex";
import Logo from "@/images/logo.png";

import "./HistoryLetters.css";

const selectTab = (event) => {
  if (event.target.className.indexOf("history-letters__tab-item") !== -1) {
    document.querySelectorAll(".history-letters__tab-item").forEach((item) => {
      item.classList.remove("history-letters__tab-item_active");
    });
    event.target.classList.add("history-letters__tab-item_active");
  }
};

function test(data) {
  console.log(data);
}

export default {
  name: "HistoryLetters",
  components: {
    HeaderLc,
  },
  data: () => {
    return {
      Logo,
    };
  },
  computed: {
    ...mapState({
      account: (state) => state.account,
    }),
  },
  methods: {
    ...mapActions("account", ["logout"]),
    selectTab,
    test,
  },
};
</script>