            <svg
              width="12"
              height="9"
              viewBox="0 0 12 9"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M1 4.06114L4.39416 7.32485L10.973 1"
                stroke="white"
                stroke-width="1.5"
                stroke-linecap="round"
              />
            </svg>
          </div>
          <div></div>
          <label for="checkbox">
            Я принимаю условия
            <router-link to>пользовательского соглашения</router-link>
          </label>
        </div>
        <div class="registration__btn-container">
          <router-link
            to="/sign-in"
            class="registration__log-in registration__log-in_green"
          >Есть аккаунт? Войти</router-link>
          <button
            :disabled="isDisabled"
            type="submit"
            class="registration__log-in"
          >Зарегистрироваться</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { mapActions, mapState } from "vuex";

import Logo from "@/images/signIn/logo.png";

import "./Registration.css";

const passwordVisible = ref(false);
const passwordConfirmVisible = ref(false);
const registrationSuccess = ref(false);

const hidePassword = (confirm) => {
  if (confirm) {
    passwordConfirmVisible.value = !passwordConfirmVisible.value;
  } else {
    passwordVisible.value = !passwordVisible.value;
  }
};

function handleSubmit(e) {
  e.preventDefault();
  const {
    lastName,
    firstName,
    secondName,
    telephone,
    email,
    password,
    confirmPassword,
    consent,
  } = this;
  if (
    lastName &&
    firstName &&
    secondName &&
    telephone &&
    email &&
    password &&
    confirmPassword &&
    consent
  ) {
    const user = {
      Fam: lastName,
      Im: firstName,
      Otch: secondName,
      Mail: email,
      Phone: telephone,
      Password: password,
    };
    this.register(user);
  }
}

export default {
  name: "Registration",
  data: () => {
    return {
      Logo,
      passwordVisible,
      passwordConfirmVisible,
      lastName: "",
      firstName: "",
      secondName: "",
      telephone: "",
      email: "",
      password: "",
      confirmPassword: "",
      consent: false,
      registrationSuccess,
    };
  },

  methods: {
    ...mapActions("account", ["register"]),
    hidePassword,
    handleSubmit,
  },
  computed: {
    isDisabled: function () {
      if (
        this.lastName &&
        this.firstName &&
        this.secondName &&
        this.telephone &&
        this.email &&
        this.password &&
        this.confirmPassword &&
        this.consent
      ) {
        return false;
      }
      return true;
    },
    ...mapState({
      account: (state) => state.account,
    }),
  },
  unmounted: function () {
    this.account.status.error = "";
  },
};
</script>