</template>

<script>
import { ref } from "vue";
import "./LetterItem.css";

const itemVisible = ref(false);

function getItemClasses() {
  let className = "";

  if (this.item.read) {
    className += " letter-item_read ";
  }

  if (this.visible) {
    className += " letter-item_visible ";
  }

  if (this.class) {
    className += this.class;
  }

  return className;
}

function openAccordion() {
  this.visible = !this.visible;
}

export default {
  name: "LetterItem",
  props: {
    item: Object,
    index: Number,
    class: String,
  },
  data: () => {
    return {
      itemVisible,
      visible: false,
    };
  },
  computed: {
    getItemClasses,
  },
  methods: {
    openAccordion,
  },
};
</script>