<script>
import { ref } from "vue";

import Tabs from "@/components/subcomponents/tabs/Tabs.vue";

const currentDocFile = ref("");
const currentPowerFile = ref("");
const currentEntityFile = ref("");

function togglePowerForm() {
  this.$refs.powerForm.classList.toggle("settings__add-item_open");
}

function toggleEntityForm() {
  this.$refs.entityForm.classList.toggle("settings__add-item_open");
}

function setDocFileName(e) {
  let files = e.target.files || e.dataTransfer.files;
  currentDocFile.value = files[0].name;
}

function resetDocFile() {
  this.$refs.inputFileDoc.value = null;
  currentDocFile.value = this.$refs.inputFileDoc.value;
}

function setPowerFileName(e) {
  let files = e.target.files || e.dataTransfer.files;
  currentPowerFile.value = files[0].name;
}

function resetPowerFile() {
  this.$refs.inputFilePower.value = null;
  currentPowerFile.value = this.$refs.inputFilePower.value;
}

function setEntityFileName(e) {
  let files = e.target.files || e.dataTransfer.files;
  currentEntityFile.value = files[0].name;
}

function resetEntityFile() {
  this.$refs.inputFileEntity.value = null;
  currentEntityFile.value = this.$refs.inputFileEntity.value;
}

const tabsItems = [
  {
    name: "Контактная информация",
    path: "/settings/contact",
  },
  {
    name: "Персональные данные",
    path: "/settings/personal",
  },
  {
    name: "Платежные данные",
    path: "/settings/payment",
  },
  {
    name: "Безопасность аккаунта",
    path: "/settings/security",
  },
];

export default {
  name: "SettingsPersonal",
  components: {
    Tabs,
  },
  data: () => {
    return {
      tabsItems,
      currentDocFile,
      currentPowerFile,
      currentEntityFile,
    };
  },
  methods: {
    setDocFileName,
    resetDocFile,
    setPowerFileName,
    resetPowerFile,
    togglePowerForm,
    toggleEntityForm,
    setEntityFileName,
    resetEntityFile
  },
};
</script>